const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL || ''

interface PredictionResponse {
  success: boolean
  predicciones: number[]
  error?: string
}

export async function getPrediction(diasFuturos: number): Promise<number[]> {
  try {
    // Llamar a la ruta API de Next.js, no directamente al backend Flask
    const response = await fetch('/api/predict', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        dias_futuros: diasFuturos,
      }),
    })

    if (!response.ok) {
      const errorData = await response.json().catch(() => ({}))
      throw new Error(errorData.error || `Error en la predicci√≥n: ${response.statusText}`)
    }

    const data: PredictionResponse = await response.json()

    if (data.error || !data.success) {
      throw new Error(data.error || 'Error al generar predicci√≥n')
    }

    return data.predicciones
  } catch (error) {
    console.error('[API] Error fetching prediction:', error)
    throw error
  }
}